import './Public.css';

import { useTheme } from '@mui/material/styles';
import Grid from '@mui/material/Grid';

import LinkOutlinedIcon from '@mui/icons-material/LinkOutlined';

function PortfolioCard(props) {
  const { projectData } = props;

  const theme = useTheme();

  return (
    <div>
      <Grid container direction='column' gap={3}>
        <Grid item>
          <div
            className='image-container'
            style={{
              color: theme.palette.primary.main,
            }}
          >
            {projectData?.icon}
          </div>
        </Grid>
        <Grid item>
          <Grid container direction='column' gap={1}>
            <Grid item>
              <div className='font-subheader color-fontmain'>
                {projectData.title}
              </div>
            </Grid>
            <Grid item>
              <Grid container spacing={1}>
                {projectData?.tag?.map((data, index) => {
                  return (
                    <Grid item key={'tagItem' + index}>
                      <div
                        className='tag font-tag'
                        style={{
                          backgroundColor: theme.palette.secondary.main,
                          color: theme.palette.primary.main,
                        }}
                      >
                        {data}
                      </div>
                    </Grid>
                  );
                })}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item>
          <div className='font-subbody color-fontsub'>
            {projectData.description}
          </div>
        </Grid>
        <Grid item>
          <Grid container direction='row' alignItems='center' gap={1}>
            <Grid item>
              <LinkOutlinedIcon
                style={{
                  color: theme.palette.primary.main,
                  fontSize: '16px',
                }}
              />
            </Grid>
            <Grid item>
              <div
                className='font-subbodybold'
                style={{
                  color: theme.palette.primary.main,
                }}
              >
                {projectData.linkname}
              </div>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

export default PortfolioCard;
