import React, { useState, useLayoutEffect } from 'react';
import '../components/Public.css';

import { useTheme } from '@mui/material/styles';
import { useParams, useNavigate } from 'react-router-dom';

function Test(props) {
  const { data } = props;
  const { id } = useParams();
  const found = data.find((u) => u.path === id);
  const navigate = useNavigate();

  if (found) {
    {
      console.log(
        data.findIndex((object) => object.path === id),
        id,
        data
      );
    }
    return (
      <div className='font-header' style={{ paddingTop: '100px' }}>
        {data[data.findIndex((object) => object.path === id)].detail}
      </div>
    );
  } else {
    navigate('/');
    return null;
  }
}

export default Test;
