import { Link } from 'react-router-dom';
import './Public.css';

import Grid from '@mui/material/Grid';

import SectionTitle from './SectionTitle';
import PortfolioCard from './PortfolioCard';
import SectionNoData from './SectionNoData';
import { portfolio_data } from '../data/Portfolio';

import { SiteButton } from './SiteButton';

function HomePortfolioSection(props) {
  return (
    <Grid container direction='column' gap={6}>
      <Grid item>
        <SectionTitle
          title={portfolio_data[0]?.data[0]?.title}
          subtitle={portfolio_data[0]?.data[0]?.subtitle}
        />
      </Grid>
      <Grid item>
        {portfolio_data[0]?.list ? (
          <Grid container rowSpacing={6} columnSpacing={3}>
            {portfolio_data[0]?.list.slice(0, 3)?.map((data, index) => {
              return (
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={4}
                  style={{ width: '100%' }}
                  key={'projectItem' + index}
                >
                  <PortfolioCard projectData={data} />
                </Grid>
              );
            })}
          </Grid>
        ) : (
          <SectionNoData />
        )}
      </Grid>
      {portfolio_data[0]?.list ? (
        <Grid item>
          <SiteButton variant='outlined' component={Link} to='/portfolio'>
            All Projects
          </SiteButton>
        </Grid>
      ) : undefined}
    </Grid>
  );
}

export default HomePortfolioSection;
