import './Public.css';

import { useTheme } from '@mui/material/styles';
import Grid from '@mui/material/Grid';

import LinkOutlinedIcon from '@mui/icons-material/LinkOutlined';

function CertificateCard(props) {
  const { certificateData } = props;

  const theme = useTheme();

  return (
    <div>
      <Grid container direction='column' gap={3}>
        <Grid item>
          <div className='image-container'>
            <img
              src={certificateData?.image_path}
              className='image-style-inside'
            />
          </div>
        </Grid>
        <Grid item>
          <Grid container direction='column' gap={1}>
            <Grid item>
              <div className='font-subheader color-fontmain'>
                {certificateData.title}
              </div>
            </Grid>
            <Grid item>
              <Grid container spacing={1}>
                {certificateData?.tag?.map((data, index) => {
                  return (
                    <Grid item key={'tagItem' + index}>
                      <div
                        className='tag font-tag'
                        style={{
                          backgroundColor: theme.palette.secondary.main,
                          color: theme.palette.primary.main,
                        }}
                      >
                        {data}
                      </div>
                    </Grid>
                  );
                })}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item>
          <div className='font-subbody color-fontsub'>
            {certificateData.description}
          </div>
        </Grid>
        <Grid item>
          <div className='font-subbody color-fontsub'>
            Certified {certificateData.certified}
          </div>
        </Grid>
        {certificateData.type === 'link' ? (
          <Grid item>
            <Grid container direction='row' alignItems='center' gap={1}>
              <Grid item>
                <LinkOutlinedIcon
                  style={{
                    color: theme.palette.primary.main,
                    fontSize: '16px',
                  }}
                />
              </Grid>
              <Grid item>
                <div
                  className='font-subbodybold'
                  style={{
                    color: theme.palette.primary.main,
                  }}
                >
                  {certificateData.linkname}
                </div>
              </Grid>
            </Grid>
          </Grid>
        ) : undefined}
      </Grid>
    </div>
  );
}

export default CertificateCard;
